import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import Test from '@/views/Test.vue'
import Content from '@/views/Content.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/content',
      name: 'Content',
      component: Content
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    }
  ]
})



